html,
body {
  height: 100%;
  width: 100%;
  font-family: "Segoe UI", sans-serif;
  overflow: hidden;
  transition: background-color 0.6s ease-in-out;
}

body.light {
  background-color: #ffffff;
  color: #000000;
}

body.dark {
  background-color: #111111;
  color: #ffffff;
}

.owl-toggle {
  position: fixed;
  top: 2vh;
  left: 2vw;
  z-index: 1002;
  background: none;
  border: none;
  cursor: pointer;
  width: 10vw;
  max-width: 60px;
  height: auto;
}

.owl-toggle svg {
  width: 100%;
  height: auto;
}

.eye-open,
.eye-closed {
  display: block;
}

.hidden {
  display: none !important;
}

h1 {
  text-align: center;
  margin-top: 10vh;
  font-size: clamp(1.5rem, 4vw, 3rem);
  z-index: 1;
  position: relative;
}

.transition-overlay {
  position: fixed;
  width: 10vw;
  height: 10vw;
  max-width: 120px;
  max-height: 120px;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
  pointer-events: none;
  border-radius: 50%;
  transform: scale(0);
  transform-origin: center;
}

.zoom-in {
  top: -15vh;
  left: 100vw;
  transform: scale(0);
}

.expand {
  animation: enterFromTopRight 2.5s forwards ease-in;
}

.shrink {
  animation: exitToBottomLeft 2.5s forwards ease-in;
}

@keyframes enterFromTopRight {
  0% {
    top: -15vh;
    left: 100vw;
    transform: scale(0);
  }
  30% {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.4);
  }
  100% {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(30);
  }
}

@keyframes exitToBottomLeft {
  0% {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(30);
  }
  50% {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.4);
  }
  100% {
    top: 110vh;
    left: -10vw;
    transform: scale(0);
  }
}

@media screen and (max-width: 600px) {
  .owl-toggle {
    width: 14vw;
    max-width: 50px;
  }

  h1 {
    font-size: 6vw;
  }
}
